#App {
    font-family: sans-serif;
    text-align: center;
    background-image: url("https://images.pexels.com/photos/131687/pexels-photo-131687.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500");
    width: 100%;
    height: auto;
    object-fit: cover;
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    justify-content: space-around;
}

#title {
    background-color: rgb(63, 61, 61);
    color: glow;
    background-image: url("https://images.pexels.com/photos/172302/pexels-photo-172302.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500");
    width: 85%;
    height: auto;
    margin: auto;
    object-fit: cover;
    font-weight: bolder;
    border-radius: 45px;
    padding: 10px 10px 10px 10px;
}

#poster {
    object-fit: cover;
    padding-top: 10px;
    width: 80%;
    object-fit: cover;
}

.btn {
    font-size: 40px;
}